body {
  font-family: Tahoma, sans-serif;
}

.container-global {
  width: 80%;
  margin: 0 auto;
}

.container-loader {
  text-align: center;
}

.loader {
  width: 80px;
  height: 80px;
  box-sizing: border-box;
  border: 8px solid #eac3b8;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  margin-top: 20px;
  margin-bottom: 20px;
}

.cat-info {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.cat-img {
  width: 30%;
  height: auto;
  border-radius: 5px;
  display: block;
}

.cat-description {
  font-size: 18px;
}

.cat-name {
  margin-top: 0;
  font-size: 25px;
}

.cat-temperament {
  font-weight: 600;
}

.is-hidden {
  display: none;
}

